.category-products {
  @media (min-width: 768px) {
    position: relative;
    margin: 0;
    margin-top: -119px;
  }
}

.category-products::after {
  @media (min-width: 768px) {
    content: "";
    position: absolute;
    bottom: -24px;
    left: 0;
    width: 100%;
    height: 34px;
    background: url("../img/bg-zigzag-fill.svg");
    background-repeat: repeat-x;
    background-size: auto 23px;
    z-index: 1;
  }

  @media (min-width: 1150px) {
    background-size: 1150px;
    bottom: -20px;
  }
}

.category-products__link {
  display: block;
  font-weight: bold;
  font-size: 19px;
  line-height: 1.3;
  padding: 24px 30px;
  color: #ffffff;
  height: 103px;

  @media (min-width: 768px) {
    font-size: 20px;
    height: 119px;
    padding: 27px 35px;
    line-height: 1.54;
  }

  &--interior {
    background: #63d1bb url("../img/icon-interior.svg") 86% 50% no-repeat;

    @media (min-width: 768px) {
      background-position-x: 88%;
    }

    @media (min-width: 1150px) {
      background-position-x: 92%;
    }
  }

  &--toy {
    background: #56c3b2 url("../img/icon-toy.svg") 90% 50% no-repeat;

    @media (min-width: 768px) {
      background-position-x: 88%;
    }

    @media (min-width: 1150px) {
      background-position-x: 92%;
    }
  }
}

.category-products__list {
  margin: 0;
  list-style-type: none;
  padding: 0;

  @media (min-width: 768px) {
    display: flex;
    flex-wrap: wrap;

    @include centering();
  }
}

.category-products__item {
  @media (min-width: 768px) {
    width: 50%;
  }
}

.popular-product__wrapper-content {
  @media (min-width: 1150px) {
    margin-left: 173px;
  }
}
